import classes from "./Login.module.css";
import React from "react";
//import Header from "./Components/Header/Header";
import Header from "../../LandingPage/Header";
import clsx from "clsx";
import Footer from "../../LandingPage/Footer";
import LoginForm from "./Components/LoginForm/LoginForm";

import "../../../Styles/CSS/brands.min.css";

window.onload = function () {
  const headerInner = document.getElementById("header_inner");

  if (headerInner)
    document.getElementById("header_inner").className = "header-sticked";

  if (document.getElementById("top_logo"))
    document.getElementById("top_logo").src = "images/top-logo2.svg";
};

const DesktopLogin = () => {
  document.querySelector("body").style.maxWidth = "unset";

  return (
    <>
      <section className={classes.Desktop_Login}>
        <Header />
        <div className={classes.Container}>
          <div className={clsx("row")}>
            <div className={clsx("col-md-12 col-lg-6 ")}>
              <div className={clsx(classes.Tag_Line)}>
                <h1>Take your business digital!</h1>
                <img src={"images/rocket.jpg"} alt="rocket" />
              </div>
            </div>
            <div
              className={clsx("col-md-12 col-lg-6", classes.Login_Container)}
            >
              <div>
                <LoginForm />
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};
export default DesktopLogin;
