import {
  Card,
  IconButton,
  InputAdornment,
  Typography,
  withStyles,
} from "@material-ui/core";
import { TextField } from "@mui/material";
import clsx from "clsx";
import { useCallback, useEffect } from "react";
import { updateObject } from "../../../../../Shared/Utility";
import UIButton from "../../../../UI/UIButton/UIButton";
import classes from "./LoginForm.module.css";
import firebase from "../../../../../firebase";
import { useAuth } from "../../../../../contexts/AuthContext";
import { useToastMessage } from "../../../../../contexts";
import { useInterval } from "../../../../../hooks/useInterval";
import { validateMobileNumber } from "../../../../../api/Auth";

import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/material.css";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import useLocalStorage from "hooks/useLocalStorage";

const CssTextField = withStyles({
  root: {
    "& label.Mui-focused": {
      color: "#000000;",
      opacity: 0.5,
    },
    "& .MuiFilledInput-underline:after": {
      border: "none !important",
    },
    "& .MuiFilledInput-underline:before": {
      border: "none !important",
    },
    "& .MuiFilledInput-root": {
      "background-color": "unset",
      "& fieldset": {
        borderColor: "#000000;",
        opacity: 0.1,
        borderRadius: "5px !important",
      },
      "&:hover fieldset": {
        borderColor: "#000000;",
        opacity: 0.3,
      },
      "&.Mui-focused fieldset": {
        borderColor: "#000000;",
        opacity: 0.3,
      },
    },
  },
})(TextField);

const LoginForm = () => {
  const {
    // eslint-disable-next-line no-unused-vars
    token,
    authSteps,
    setAuthStep,
    signInWithNumber,
    submitPhoneNumberAuthCode,
    checkUserAuthStatus,
    formData,
    setFormData,
    currentLoginState,
    setCurrentLoginState,
    signInWithPassword,
    resendOTP,
    setResendOTP,
  } = useAuth();
  const [callingCode, setCallingCode] = useLocalStorage("callingCode", null);
  const [country, setCountry] = useLocalStorage("country", null);
  const getCountry = useCallback(async () => {
    try {
      const visitorReq = await fetch("//api.ipregistry.co/?key=tryout", {
        method: "GET",
      });
      if (visitorReq.ok) {
        const visitor = await visitorReq.json();
        setCallingCode(visitor?.location?.country?.calling_code);
        setCountry(visitor?.location?.country?.code.toLowerCase());
      } else {
        setCountry("in");
      }
    } catch (error) {
      console.error(error);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [setCountry]);

  const toastMessage = useToastMessage();

  const inputChangedHandler = (event, inputIdentifier) => {
    let updatedFormElement;

    updatedFormElement = updateObject(formData[inputIdentifier], {
      value:
        currentLoginState === "OTP"
          ? event.target.value.length < formData[inputIdentifier].maxLength
            ? event.target.value
            : event.target.value.slice(0, formData[inputIdentifier].maxLength)
          : event.target.value,
    });

    const updatedOrderForm = updateObject(formData, {
      [inputIdentifier]: updatedFormElement,
    });

    setFormData(updatedOrderForm);
  };

  const inputChangedFlag = (value) => {
    if (value.length !== 7) {
      const updatedOrderForm = updateObject(formData, {
        number: {
          value,
        },
      });
      setFormData(updatedOrderForm);
    }
  };

  const clearFieldValue = (fieldName) => {
    return setFormData((prevState) => ({
      ...prevState,
      [fieldName]: { ...prevState[fieldName], value: "" },
    }));
  };

  const onFormSubmit = async (e) => {
    e.preventDefault();

    toastMessage.setDisabled();

    // can't login with email using OTP
    if (!parseInt(formData.number.value) && currentLoginState === "OTP") {
      return toastMessage.setErrorMessage(
        "Cannot login with OTP using Email Address!"
      );
    }

    // currentStatsus is not otp
    // Signs in with password
    if (currentLoginState === "Password") {
      const password = formData.otp.value;

      setFormData((prevState) => ({
        ...prevState,
        otp: {
          ...prevState.otp,
          value: password,
        },
      }));

      return signInWithPassword(
        formData.number.value,
        password,
        toastMessage,
        false
      );
    }

    // these below code signs in with OTP
    if (authSteps === 0) {
      const res = await validateMobileNumber({
        number: `+${formData.number.value}`,
      });

      if (res.error) {
        return toastMessage.setInfoMessage(res.error);
      }

      return signInWithNumber(formData.number.value, toastMessage, false);
    }

    if (authSteps !== 0 && formData.otp.value.length > 5) {
      return submitPhoneNumberAuthCode(formData.otp.value, toastMessage, false);
    }
  };

  // eslint-disable-next-line no-unused-vars
  const authCaptchaVerifier = () => {
    window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier(
      "captcha-container",
      {
        size: "invisible",
        callback: function (response) {
          signInWithNumber(formData.number.value, toastMessage);
        },
        "expired-callback": function () {
          // Response expired. Ask user to solve reCAPTCHA again.
          // ...
          alert("try again!");
        },
      }
    );
  };

  useEffect(() => {
    authSteps > 0 &&
      setFormData((prevState) => ({
        ...prevState,
        otp: { ...prevState.otp, disabled: false },
      }));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [authSteps]);

  useEffect(() => {
    // // setAuthStep(0);

    // if (!token) {
    //   authCaptchaVerifier();
    // }

    checkUserAuthStatus();
    setAuthStep(0);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useInterval(
    () => {
      setResendOTP((prevState) => ({
        ...prevState,
        disabled: prevState.counter > 0 ? true : false,
        counter: prevState.counter - 1,
        startTimer: prevState.counter > 0 ? true : false,
      }));
    },
    authSteps === 1 &&
      currentLoginState === "OTP" &&
      resendOTP.counter >= 0 &&
      resendOTP.startTimer
      ? 1000
      : null
  );

  const formElementsArray = [];
  for (let key in formData) {
    formElementsArray.push({
      id: key,
      config: formData[key],
    });
  }

  useEffect(() => {
    (country === null || callingCode === null) && getCountry();
  }, [country, callingCode, getCountry]);

  return (
    <div>
      <Card className={` ${classes.Container} ${classes.rightForm}`}>
        <form onSubmit={onFormSubmit}>
          {/* <Loader /> */}
          <div className={clsx("col-10  m-auto pt-5 pb-3", classes.Form_Title)}>
            <h1>
              {sessionStorage.getItem("invitation")
                ? "Sign In and accept invite"
                : "Sign In, to get started!"}
            </h1>
          </div>
          <div className="col-10 m-auto">
            {(formData.number.value.length > 0 &&
              formData.number.value.charAt(0) === "+") ||
            (formData.number.value.length > 6 &&
              `${parseInt(formData.number.value)}`.length ===
                formData.number.value.length) ? (
              <div className={clsx("col-12 mt-4", classes.Form_Input)}>
                <PhoneInput
                  value={
                    formData.number.value.length === 7
                      ? formData.number.value.includes(callingCode)
                        ? formData.number.value
                        : `${callingCode}${formData.number.value}`
                      : formData.number.value
                  }
                  country={country}
                  enableAreaCodes={true}
                  onChange={(phone) => inputChangedFlag(phone)}
                  label="Mobile No."
                  inputProps={{
                    autoFocus: true,
                  }}
                />{" "}
              </div>
            ) : (
              <CssTextField
                className={clsx("col-12 mt-4", classes.Form_Input)}
                label={
                  (formData.number.value.length > 0 &&
                    formData.number.value.charAt(0) === "+") ||
                  formData.number.value.length > 6
                    ? `${parseInt(formData.number.value)}`.length ===
                      formData.number.value.length
                      ? "Mobile No."
                      : "Email"
                    : "Email or Mobile No."
                }
                type={
                  (formData.number.value.length > 0 &&
                    formData.number.value.charAt(0) === "+") ||
                  formData.number.value.length > 6
                    ? `${parseInt(formData.number.value)}`.length ===
                      formData.number.value.length
                      ? "number"
                      : "email"
                    : "email"
                }
                onChange={(e) => inputChangedHandler(e, "number")}
                value={formData.number.value}
                name="mobile number"
                variant="filled"
                disabled={formData.number.disabled}
                inputProps={{
                  autoFocus: true,
                  required: true,
                }}
                InputLabelProps={{ shrink: true }}
              />
            )}
            <CssTextField
              required
              className={clsx("col-12 mt-4", classes.Form_Input)}
              label={currentLoginState === "OTP" ? "OTP" : "Password"}
              onChange={(e) => inputChangedHandler(e, "otp")}
              value={formData.otp.value}
              variant="filled"
              InputLabelProps={{ shrink: true }}
              inputProps={{
                type: formData.otp.type,
                name: "otp",
                disabled:
                  authSteps === 0 && currentLoginState === "OTP"
                    ? formData.otp.disabled
                    : false,
                required: currentLoginState !== "OTP" ? true : false,
              }}
              pattern={currentLoginState === "OTP" ? "[0-9]*" : ""}
              InputProps={{
                style: {
                  backgroundColor: "#f2f3f7",
                  borderRadius: "5px",
                },
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={() => {
                        const updatedOrderForm = updateObject(formData, {
                          otp: {
                            ...formData.otp,
                            type:
                              formData.otp.type === "password"
                                ? "text"
                                : "password",
                          },
                        });
                        setFormData(updatedOrderForm);
                      }}
                      edge="end"
                    >
                      {formData.otp.type === "password" ? (
                        <Visibility />
                      ) : (
                        <VisibilityOff />
                      )}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />

            {/* {currentLoginState === "OTP" && (
            <Typography
              variant="p"
              style={{
                color: "#7b8aa2",
                opacity: 0.7,
                fontSize: "11px",
              }}
            >
              {currentLoginState} verifies mobile no. and avoid spammers
            </Typography>
          )} */}

            {authSteps !== 0 && currentLoginState === "OTP" && (
              <div className="d-flex justify-content-end mt-1">
                <p
                  style={{
                    display: "flex",
                    justifyContent: "flex-end",
                    width: "90%",
                    opacity: resendOTP.disabled ? 0.3 : 0.6,
                    pointerEvents: resendOTP.disabled ? "none" : "all",
                    cursor: resendOTP.disabled ? "not-allowed" : "pointer",
                  }}
                  onClick={() => {
                    signInWithNumber(`${formData.number.value}`, toastMessage);

                    setResendOTP((prevState) => ({
                      ...prevState,
                      disabled: true,
                      counter: 30,
                      startTimer: true,
                    }));
                  }}
                >
                  Resend OTP
                  {resendOTP.counter < 30 &&
                    resendOTP.counter > 0 &&
                    ` in ${resendOTP.counter}s`}
                </p>
              </div>
            )}
          </div>

          <div className={clsx(classes.Submit_Container, "col-10 m-auto")}>
            <UIButton
              style={{ marginLeft: 0 }}
              showLoader={toastMessage.isDisabled}
              disabled={
                toastMessage.isDisabled || formData.number.value.length < 10
              }
              className="mt-4 ml-1 mb-3"
            >
              {authSteps === 0 && currentLoginState === "OTP"
                ? "Get OTP"
                : "Sign In"}
            </UIButton>
            {/* Disabled OTP Login */}
            {false && (
              <Typography
                onClick={() => {
                  // authSteps !== 0 && window.location.reload();
                  clearFieldValue("otp");
                  setCurrentLoginState((prevState) =>
                    prevState === "OTP" ? "PASSWORD" : "OTP"
                  );
                }}
                variant="caption"
                style={{
                  cursor: "pointer",
                  color: "#24272C",
                  fontSize: "15px",
                  fontWeight: 500,
                  lineHeight: "18px",
                  textDecoration: "none",
                }}
              >
                Login using {currentLoginState === "OTP" ? "Password" : "OTP"}
              </Typography>
            )}
          </div>
          <div className={clsx(classes.Submit_Container, "col-10 m-auto mb-5")}>
            <Typography
              onClick={() => window.open("/forgot-password", "_self")}
              variant="caption"
              style={{
                cursor: "pointer",
                color: "#24272C",
                fontSize: "15px",
                fontWeight: 500,
                lineHeight: "18px",
                textDecoration: "underline",
              }}
            >
              Forgot Password?
            </Typography>
          </div>
        </form>
        <div id="captcha-container"></div>
      </Card>
      <div className="text-center my-4">
        <p
          style={{
            color: "#24272C",
            fontSize: "18px",
            fontWeight: 500,
            lineHeight: "21px",
          }}
        >
          {"Don’t have an account? "}
          <Typography
            onClick={() => window.open("/sign-up", "_self")}
            variant="caption"
            style={{
              cursor: "pointer",
              color: "#12B0EE",
              fontSize: "18px",
              fontWeight: 500,
              lineHeight: "21px",
              textDecoration: "none",
            }}
          >
            Sign Up
          </Typography>
        </p>
      </div>
    </div>
  );
};

export default LoginForm;
