/** @format */

import { useCallback, useEffect, useRef } from "react";
import classes from "./Signup.module.css";
import firebase from "../../../firebase";
import { updateObject } from "../../../Shared/Utility";
import LogoSection from "../../UI/LogoSection/LogoSection";
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import { UIButton } from "../../exports";
import { useAuth } from "../../../contexts/AuthContext";
import clsx from "clsx";
import {
  Typography,
  withStyles,
  IconButton,
  InputAdornment,
} from "@material-ui/core";
import { TextField } from "@mui/material";
import { ArrowBackIos, Visibility, VisibilityOff } from "@material-ui/icons";
import { withRouter } from "react-router-dom/cjs/react-router-dom.min";
import { useInterval } from "../../../hooks/useInterval";
import { validateMobileNumber } from "../../../api/Auth";

import PhoneInput from "react-phone-input-2";
import useLocalStorage from "hooks/useLocalStorage";

const CssTextField = withStyles({
  root: {
    "& label.Mui-focused": {
      color: "#000000;",
      opacity: 0.5,
    },
    "& .MuiFilledInput-underline:after": {
      border: "none !important",
    },
    "& .MuiFilledInput-underline:before": {
      border: "none !important",
    },
    "& .MuiFilledInput-root": {
      "background-color": "unset",
      "& fieldset": {
        borderColor: "#000000;",
        opacity: 0.1,
        borderRadius: "5px !important",
      },
      "&:hover fieldset": {
        borderColor: "#000000;",
        opacity: 0.3,
      },
      "&.Mui-focused fieldset": {
        borderColor: "#000000;",
        opacity: 0.3,
      },
    },
  },
})(TextField);

const Signup = ({ history, ...props }) => {
  const toastMessage = useToastMessage();
  const {
    formData,
    setFormData,
    authSteps,
    setAuthStep,
    checkUserAuthStatus,
    currentLoginState,
    setCurrentLoginState,
    signInWithPassword,
    signInWithNumber,
    submitPhoneNumberAuthCode,
    resendOTP,
    setResendOTP,
  } = useAuth();
  const [callingCode, setCallingCode] = useLocalStorage("callingCode", null);
  const [country, setCountry] = useLocalStorage("country", null);
  const getCountry = useCallback(async () => {
    try {
      const visitorReq = await fetch("//api.ipregistry.co/?key=tryout", {
        method: "GET",
      });
      if (visitorReq.ok) {
        const visitor = await visitorReq.json();
        setCallingCode(visitor?.location?.country?.calling_code);
        setCountry(visitor?.location?.country?.code.toLowerCase());
      } else {
        setCountry("in");
      }
    } catch (error) {
      console.error(error);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [setCountry]);

  const inputRef = useRef();

  const inputChangedHandler = (event, inputIdentifier) => {
    let updatedFormElement;

    updatedFormElement = updateObject(formData[inputIdentifier], {
      value:
        currentLoginState === "OTP"
          ? event.target.value.length < formData[inputIdentifier].maxLength
            ? event.target.value
            : event.target.value.slice(0, formData[inputIdentifier].maxLength)
          : event.target.value,
    });

    const updatedOrderForm = updateObject(formData, {
      [inputIdentifier]: updatedFormElement,
    });

    setFormData(updatedOrderForm);
  };

  const inputChangedFlag = (value) => {
    if (value.length !== 7) {
      const updatedOrderForm = updateObject(formData, {
        number: {
          value,
        },
      });
      setFormData(updatedOrderForm);
    }
  };

  const authCaptchaVerifier = () => {
    window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier(
      "captcha-container",
      {
        size: "invisible",
        callback: function (response) {
          signInWithNumber(formData.number.value, toastMessage);
        },
        "expired-callback": function () {
          // Response expired. Ask user to solve reCAPTCHA again.
          // ...
          alert("try again!");
        },
      }
    );
  };

  useEffect(() => {
    if (!localStorage.getItem("token")) {
      authCaptchaVerifier();
    }

    checkUserAuthStatus();
    setAuthStep(0);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onFormSubmit = async (e) => {
    e.preventDefault();

    // can't login with email using OTP
    if (!parseInt(formData.number.value) && currentLoginState === "OTP") {
      return toastMessage.setErrorMessage(
        "Cannot login with OTP using Email Address!"
      );
    }

    // currentStatsus is not otp
    // Signs in with password
    if (currentLoginState === "Password") {
      const encPass = formData.otp.value;

      setFormData((prevState) => ({
        ...prevState,
        otp: {
          ...prevState.otp,
          value: encPass,
        },
      }));

      return signInWithPassword(formData.number.value, encPass, toastMessage);
    }

    // these below code signs in with OTP
    if (authSteps === 0) {
      const res = await validateMobileNumber({
        number: `+${formData.number.value}`,
      });

      if (res.error) {
        return toastMessage.setErrorMessage(
          res.code ? "Please enter valid Mobile Number" : res.error
        );
      }

      return signInWithNumber(formData.number.value, toastMessage);
    }

    if (authSteps !== 0 && formData.otp.value.length > 5) {
      return submitPhoneNumberAuthCode(formData.otp.value, toastMessage);
    }
  };

  const clearFieldValue = (fieldName) => {
    return setFormData((prevState) => ({
      ...prevState,
      [fieldName]: { ...prevState[fieldName], value: "" },
    }));
  };

  useInterval(
    () => {
      setResendOTP((prevState) => ({
        ...prevState,
        disabled: prevState.counter > 0 ? true : false,
        counter: prevState.counter - 1,
        startTimer: prevState.counter > 0 ? true : false,
      }));
    },
    authSteps === 1 &&
      currentLoginState === "OTP" &&
      resendOTP.counter >= 0 &&
      resendOTP.startTimer
      ? 1000
      : null
  );

  useEffect(() => {
    (country === null || callingCode === null) && getCountry();
  }, [country, callingCode, getCountry]);

  return (
    <div className={classes.Signup}>
      <LogoSection />
      <div className={classes.Form_Section}>
        {/* Login Form */}
        <div className={classes.Form} ref={inputRef}>
          <div className={classes.Form_Header}>
            <IconButton onClick={() => history.goBack()}>
              <ArrowBackIos />
            </IconButton>
            <div className={classes.Header_Text}>
              <h1>Sign In, to get started!</h1>
            </div>
          </div>
          <form onSubmit={onFormSubmit}>
            <div className="col-10 m-auto">
              {(formData.number.value.length > 0 &&
                formData.number.value.charAt(0) === "+") ||
              (formData.number.value.length > 6 &&
                parseInt(formData.number.value)) ? (
                <div className={clsx("col-12 mt-4", classes.Form_Input)}>
                  <PhoneInput
                    value={
                      formData.number.value.length === 7
                        ? `${callingCode}${formData.number.value}`
                        : formData.number.value
                    }
                    country={country}
                    enableAreaCodes={true}
                    onChange={(phone) => inputChangedFlag(phone)}
                    label="Mobile No."
                    inputProps={{
                      autoFocus: true,
                    }}
                  />
                </div>
              ) : (
                <CssTextField
                  className={clsx("col-12 mt-4", classes.Form_Input)}
                  label={
                    (formData.number.value.length > 0 &&
                      formData.number.value.charAt(0) === "+") ||
                    formData.number.value.length > 6
                      ? parseInt(formData.number.value)
                        ? "Mobile No."
                        : "Email"
                      : "Email or Mobile No."
                  }
                  type={
                    (formData.number.value.length > 0 &&
                      formData.number.value.charAt(0) === "+") ||
                    formData.number.value.length > 6
                      ? parseInt(formData.number.value)
                        ? "number"
                        : "email"
                      : "email"
                  }
                  onChange={(e) => inputChangedHandler(e, "number")}
                  value={formData.number.value}
                  name="mobile number"
                  variant="filled"
                  disabled={formData.number.disabled}
                  inputProps={{
                    autoFocus: true,
                    required: true,
                  }}
                  InputLabelProps={{ shrink: true }}
                  inputMode={currentLoginState === "OTP" ? "numeric" : "email"}
                />
              )}

              <CssTextField
                className={clsx("col-12 mt-4", classes.Form_Input)}
                label={currentLoginState === "OTP" ? "OTP" : "Password"}
                onChange={(e) => inputChangedHandler(e, "otp")}
                value={formData.otp.value}
                variant="filled"
                InputLabelProps={{ shrink: true }}
                inputProps={{
                  type: formData.otp.type,
                  name: "otp",
                  disabled:
                    authSteps === 0 && currentLoginState === "OTP"
                      ? formData.otp.disabled
                      : false,
                  required: currentLoginState !== "OTP" ? true : false,
                }}
                // inputMode={currentLoginState === "OTP" ? "numeric" : "text"}
                InputProps={{
                  style: {
                    backgroundColor: "#f2f3f7",
                    borderRadius: "5px",
                  },
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={() => {
                          const updatedOrderForm = updateObject(formData, {
                            otp: {
                              ...formData.otp,
                              type:
                                formData.otp.type === "password"
                                  ? "text"
                                  : "password",
                            },
                          });
                          setFormData(updatedOrderForm);
                        }}
                        edge="end"
                      >
                        {formData.otp.type === "password" ? (
                          <Visibility />
                        ) : (
                          <VisibilityOff />
                        )}
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />

              {authSteps !== 0 && currentLoginState === "OTP" && (
                <div className="d-flex justify-content-end mt-1">
                  <p
                    style={{
                      display: "flex",
                      justifyContent: "flex-end",
                      width: "90%",
                      opacity: resendOTP.disabled ? 0.3 : 0.6,
                      pointerEvents: resendOTP.disabled ? "none" : "all",
                      cursor: resendOTP.disabled ? "not-allowed" : "pointer",
                    }}
                    onClick={() => {
                      signInWithNumber(
                        `${formData.number.value}`,
                        toastMessage
                      );
                      setResendOTP((prevState) => ({
                        ...prevState,
                        disabled: true,
                        counter: 30,
                        startTimer: true,
                      }));
                    }}
                  >
                    Resend OTP
                    {resendOTP.counter < 30 &&
                      resendOTP.counter > 0 &&
                      ` in ${resendOTP.counter}s`}
                  </p>
                </div>
              )}
            </div>
            <div className={clsx(classes.Submit_Container, "col-10 m-auto")}>
              <UIButton
                showLoader={toastMessage.isDisabled}
                style={{ marginLeft: 0 }}
                disabled={toastMessage.isDisabled}
                className="mt-3 ml-1 mb-3"
              >
                {authSteps === 0 && currentLoginState === "OTP"
                  ? "Get OTP"
                  : "Sign In"}
              </UIButton>
              {/* Disabled OTP Login */}
              {false && (
                <Typography
                  onClick={() => {
                    // authSteps !== 0 && window.location.reload();
                    clearFieldValue("otp");
                    setCurrentLoginState((prevState) =>
                      prevState === "OTP" ? "PASSWORD" : "OTP"
                    );
                  }}
                  variant="caption"
                  style={{
                    cursor: "pointer",
                    color: "#24272C",
                    fontSize: "15px",
                    fontWeight: 500,
                    lineHeight: "18px",
                    textDecoration: "none",
                  }}
                >
                  Login using {currentLoginState === "OTP" ? "Password" : "OTP"}
                </Typography>
              )}
            </div>

            <div
              className={clsx(classes.Submit_Container, "col-10 m-auto mb-3")}
            >
              <Typography
                onClick={() => window.open("/forgot-password", "_self")}
                variant="caption"
                style={{
                  cursor: "pointer",
                  color: "#24272C",
                  fontSize: "15px",
                  fontWeight: 500,
                  lineHeight: "18px",
                  textDecoration: "underline",
                }}
              >
                Forgot Password?
              </Typography>
            </div>
          </form>
        </div>

        <div className="col-10 mt-auto mx-auto text-start mb-4">
          <p
            style={{
              color: "#24272C",
              fontSize: "18px",
              fontWeight: 500,
              lineHeight: "21px",
            }}
          >
            {"Don’t have an account? "}
            <Typography
              onClick={() => window.open("/sign-up", "_self")}
              variant="caption"
              style={{
                cursor: "pointer",
                color: "#12B0EE",
                fontSize: "18px",
                fontWeight: 500,
                lineHeight: "21px",
                textDecoration: "none",
              }}
            >
              Sign Up
            </Typography>
          </p>
        </div>
      </div>
      <div id="captcha-container"></div>
    </div>
  );
};

export default withRouter(Signup);
