import { Hidden } from "@material-ui/core";
import DesktopLogin from "../../Components/Login/Desktop/Login.desktop";
import Signup from "../../Components/Login/Mobile/Signup";

const Login = () => {
  return (
    <>
      <Hidden xsDown implementation="js">
        <DesktopLogin />
      </Hidden>
      <Hidden smUp implementation="js">
        <Signup />
      </Hidden>
    </>
  );
};

export default Login;
