import React from "react";

class Header extends React.Component {
  state = {
    open: false,
  };

  render() {
    return (
      <header id="masthead">
        <div className="elementor elementor-487">
          <div className="elementor-inner" id="header_inner">
            <div className="elementor-section-wrap">
              <section
                className="elementor-section elementor-top-section elementor-element elementor-element-c847861 elementor-section-boxed elementor-section-height-default elementor-section-height-default"
                data-id="c847861"
                data-element_type="section"
                data-settings='{"background_background":"className"}'
              >
                <div className="elementor-container elementor-column-gap-default">
                  <div className="elementor-row">
                    <div
                      className="elementor-column elementor-col-33 elementor-top-column elementor-element elementor-element-d5c6807"
                      data-id="d5c6807"
                      data-element_type="column"
                    >
                      <div className="elementor-column-wrap elementor-element-populated">
                        <div className="elementor-widget-wrap">
                          <div
                            className="elementor-element elementor-element-edde17e elementor-widget elementor-widget-site-logo"
                            data-id="edde17e"
                            data-element_type="widget"
                            data-widget_type="site-logo.default"
                          >
                            <div className="elementor-widget-container">
                              <div className="hfe-site-logo">
                                <a
                                  data-elementor-open-lightbox=""
                                  className="elementor-clickable"
                                  href="/"
                                >
                                  <div className="hfe-site-logo-set">
                                    <div className="hfe-site-logo-container">
                                      <img
                                        className="hfe-site-logo-img elementor-animation- top_logo"
                                        src={"images/top-logo1.svg"}
                                        alt="logo"
                                        id="top_logo"
                                      />
                                    </div>
                                  </div>
                                </a>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div
                      className="elementor-column elementor-col-33 elementor-top-column elementor-element elementor-element-7fe2688"
                      data-id="7fe2688"
                      data-element_type="column"
                    >
                      <div className="elementor-column-wrap elementor-element-populated">
                        <div className="elementor-widget-wrap">
                          <div
                            className="elementor-element elementor-element-d169dc8 hfe-nav-menu__align-right elementor-absolute hfe-submenu-icon-arrow hfe-submenu-animation-none hfe-link-redirect-child hfe-nav-menu__breakpoint-tablet elementor-widget elementor-widget-navigation-menu"
                            data-id="d169dc8"
                            data-element_type="widget"
                            data-settings='{"_position":"absolute"}'
                            data-widget_type="navigation-menu.default"
                          >
                            <div className="elementor-widget-container">
                              <div
                                className="hfe-nav-menu hfe-layout-horizontal hfe-nav-menu-layout horizontal hfe-pointer__none"
                                data-layout="horizontal"
                              >
                                <div className="hfe-nav-menu__toggle elementor-clickable">
                                  <div className="hfe-nav-menu-icon">
                                    <i
                                      onClick={() =>
                                        this.setState({
                                          ...this.state,
                                          open: !this.state.open,
                                        })
                                      }
                                      aria-hidden="true"
                                      tabIndex="0"
                                      className="fas fa-align-justify"
                                    ></i>{" "}
                                  </div>
                                </div>
                                <nav
                                  // style={
                                  //   this.state.open
                                  //     ? {
                                  //         visibility: "visible",
                                  //         opacity: 1,
                                  //         height: "100%",
                                  //         background: "#12B0EE",
                                  //       }
                                  //     : {
                                  //         visibility: "hidden",
                                  //         opacity: 0,
                                  //         height: "0",
                                  //       }
                                  // }
                                  className={
                                    this.state.open
                                      ? "hfe-nav-menu__layout-horizontal hfe-nav-menu__submenu-arrow menu-open"
                                      : "hfe-nav-menu__layout-horizontal hfe-nav-menu__submenu-arrow"
                                  }
                                  data-toggle-icon='&lt;i aria-hidden="true" tabIndex="0" className="fas fa-align-justify"&gt;&lt;/i&gt;'
                                  data-close-icon='&lt;i aria-hidden="true" tabIndex="0" className="far fa-window-close"&gt;&lt;/i&gt;'
                                  data-full-width=""
                                >
                                  <ul
                                    // style={
                                    //   this.state.open
                                    //     ? {
                                    //         visibility: "visible",
                                    //         opacity: 1,
                                    //         boxShadow: "0px 3px 11px 0px",
                                    //       }
                                    //     : {
                                    //         visibility: "hidden",
                                    //         opacity: 0,
                                    //         boxShadow: "unset",
                                    //       }
                                    // }
                                    id="menu-1-d169dc8"
                                    className={
                                      this.state.open
                                        ? "hfe-nav-menu hfe-active-menu-active"
                                        : "hfe-nav-menu"
                                    }
                                  >
                                    <li
                                      id="menu-item-221"
                                      className="menu-item menu-item-type-post_type menu-item-object-page menu-item-home current-menu-ancestor current-menu-parent current_page_parent current_page_ancestor parent hfe-has-submenu hfe-creative-menu"
                                    >
                                      <a href="/" className="hfe-menu-item">
                                        Home
                                      </a>
                                    </li>

                                    {/* <li id="menu-item-397" className="menu-item menu-item-type-custom menu-item-object-custom parent hfe-creative-menu"><a href="/plan-pricing" className = "hfe-menu-item">Pricing & Products</a>
                            </li> */}
                                    <li
                                      id="menu-item-397"
                                      className="menu-item menu-item-type-custom menu-item-object-custom parent hfe-creative-menu"
                                    >
                                      <a
                                        href="/whatsapp-chatbot"
                                        className="hfe-menu-item"
                                      >
                                        Whatsapp Chatbot
                                      </a>
                                    </li>
                                    <li
                                      id="menu-item-397"
                                      className="menu-item menu-item-type-custom menu-item-object-custom parent hfe-creative-menu"
                                    >
                                      <a
                                        href="/contact"
                                        className="hfe-menu-item"
                                      >
                                        Contact Us
                                      </a>
                                    </li>
                                  </ul>
                                </nav>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div
                      className="elementor-column elementor-col-33 elementor-top-column elementor-element elementor-element-25c70f1"
                      data-id="25c70f1"
                      data-element_type="column"
                    >
                      <div className="elementor-column-wrap elementor-element-populated">
                        <div className="elementor-widget-wrap">
                          <div
                            className="elementor-element elementor-element-631e7d2 elementor-align-right elementor-hidden-phone elementor-tablet-align-center elementor-widget elementor-widget-button"
                            data-id="631e7d2"
                            data-element_type="widget"
                            data-widget_type="button.default"
                          >
                            <div className="elementor-widget-container">
                              <div className="elementor-button-wrapper">
                                <a
                                  href="/login"
                                  target="_blank"
                                  rel="noopener noreferrer"
                                  className="elementor-button-link elementor-button elementor-size-sm"
                                  role="button"
                                >
                                  <span className="elementor-button-content-wrapper">
                                    <span className="elementor-button-text">
                                      {localStorage.getItem("token")
                                        ? "Dashboard"
                                        : "Login"}
                                    </span>
                                  </span>
                                </a>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </section>
            </div>
          </div>
        </div>
      </header>
    );
  }
}
export default Header;
